let timer = '';
import http from '@/api/http.js'
import {checkPhone,checkPwd,checkCode} from "@/common/common.js"
import QSPopup from '@/components/QS-popup/QS-popup.vue';
export default {
	components:{QSPopup},
	data() {
		return {
			sendMsg:"获取验证码",
			isCanSendCode:true,
			phone:"",
			code:"",
			password:"",
			userId:0,
			links:'',
			isHide:true,
		};
	},
	onLoad(option) {
		console.log(option,'1111')
		
		this.userId = option.user_id
		if(option.scene){
			let scene = decodeURIComponent(option.scene)//参数二维码传递过来的参数
			this.userId = scene.split("=")[1]
			console.log(scene)
		}
		if(option.q){
			let q = decodeURIComponent(option.q)//参数二维码传递过来的参数
			console.log(q)
			this.userId = q.split("?")[1].split("=")[1]
		}
		this.getCode()
	},
	methods: {
		show(ref) {
			this.$refs[ref].show();
		},
		hides(ref) {
			this.$refs[ref].hide();
		},
		isSure(){
			this.isHide = !this.isHide
		},
		// checkToken(){
		// 	let data = {
		// 		token:uni.getStorageSync('token')
		// 	}
		// 	http('/api/User/get_userinfo','get',data).then(res=>{
				
		// 		if(res.data.status==200){
		// 			uni.switchTab({
		// 				url:'/pages/index/index'
		// 			})
		// 		}
		// 	})
		// },
		getQueryStringByName(name){
		     // let result = location.search.match(new RegExp("[\?\&]" + name+ "=([^\&]+)","i"));
		     // if(result == null || result.length < 1){
		     //     return "";
		     // }
		     // return result[1];
		},
		toLoad(){
			uni.navigateTo({
				url:'/pages/index/logon/h5Load?src='+this.links
			})
		},
		getCode(){
			http('/Api/setting/downloadInfo','post').then(res=>{
				this.links = res.data.data.link
				console.log(res)
			})
		},
		setCodeInterval () {
			//设置验证码重新发送定时器
		    let time = 60;
		    clearInterval(timer);
		    timer = setInterval(() => {
		        time--;
		        this.isCanSendCode=false;
		        this.sendMsg=time + "s"
		        if (time <= 0) {
		            this.isCanSendCode=true;
		            this.sendMsg="重新获取";
		            clearInterval(timer);
		        }
		    }, 1000)
		},
		clearCodeInterval(){
		    clearInterval(timer);
		    this.codemsg="获取验证码";
		},
		sendCode () {
			let data = {
				phone:this.phone
			}
			http('/api/Login/send_code','post',data).then(res=>{
				if(res.data.status == '200'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
				if(res.data.status == '400'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
				console.log(res)
			})
			//发送验证码
		    if (!checkPhone(this.phone)) {
		        return
		    }
		    this.setCodeInterval();
			/*
			* 发送验证码逻辑
			*/
		},
		submit() {
			if(this.phone == '' || this.password == ''|| this.code == ''){
				uni.showToast({
					title:'不能为空',
					icon:'none'
				})
				return
			}
			if(!checkPhone(this.phone)){
				return;
			};
			if(!checkPwd(this.password)){
				return;
			};
			if(!checkCode(this.code)){
				return;
			};
			if(this.isHide != true){
				uni.showToast({
					title:'请阅读并勾选协议',
					icon:'none'
				})
				return;
			}
			let data = {
				phone:this.phone,
				password:this.password,
				code:this.code,
				user_id:this.userId
			}
			http('/api/Login/registerH5','post',data).then(res=>{
				uni.setStorageSync('token',res.data.data.token)
				uni.setStorageSync('vip',res.data.data.vip)
				
				if(res.data.status == '200'){
					// uni.switchTab({
					// 	url:'/pages/index/index'
					// })
					uni.reLaunch({
						// url:'/pages/mine/mine',
						url:'/pages/mine/proinfo'
					})
				}
				if(res.data.status == '400'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
				console.log(res)
			})
		}
	}
}
